import React, { useState } from "react";
import "./src-Landing.css";

import Particles from "react-particles-js";
import { findRenderedDOMComponentWithClass } from "react-dom/test-utils";

const particleParams = (nightMode) => {
  return {
    particles: {
      size: {
        value: (0.5, 1),
      },
      color: {
        value: nightMode ? "#ffffff" : "#000000",
      },
      line_linked: {
        enable: false,
      },
      move: {
        direction: "bottom",
        speed: 0.4,
      },
      opacity: {
        value: 1,
      },
    },
  };
};
const Landing = ({ nightMode, setNightMode }) => {
  const [anim, toggle] = useState(0);
  return (
    <section id="landing-section">
      {/*
      <div
        onClick={() => {toggle(1); updateColors();}}
        onAnimationEnd={() => toggle(0)}
        className="text-center noselect"
      >
        <div anim={anim} id="vertical-strip"></div>
        </div>
      */}
      <h1
        onClick={() => setNightMode(!nightMode)}
        id="main-title"
        className="text-center noselect"
      >
        Evan Nguyen
      </h1>
      <Particles params={particleParams(nightMode)} />
    </section>
  );
};

export default Landing;
